import {
	Accordion,
	AccordionDetails,
	AccordionSummary,
	Button,
	Grid,
	Link,
	makeStyles,
	Paper,
	Typography,
} from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import Scrollbars from 'react-custom-scrollbars';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import WriteUp from '../components/WriteUp/WriteUp';
import FilterComponentProjectBank from '../components/FilterComponent/FilterComponentProjectBank';
import ResponsesButtonGroup from '../components/ResponsesButtonGroup/ResponsesButtonGroup';
import ButtonSelect from '../components/StationSelect/ButtonSelect';
import { CSVLink, CSVDownload } from 'react-csv';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
		height: '90vh',
	},
	mainComp: {
		padding: theme.spacing(3),
		height: '100%',
	},
	down: {
		paddingRight: theme.spacing(3),
		padding: theme.spacing(6),
	},
	paper1: {
		padding: theme.spacing(3),
		textAlign: 'center',
		color: theme.palette.text.primary,
		height: '200px',
		margin: '40px',
		marginTop: '10px',
		backgroundColor: 'rgba(39, 39, 39, 0.7)',
		borderColor: theme.palette.primary.main,
		borderWidth: '2px',
		borderStyle: 'solid',
	},
	paper2: {
		padding: theme.spacing(3),
		textAlign: 'center',
		color: theme.palette.text.primary,
		marginLeft: theme.spacing(6),
		marginRight: theme.spacing(3),
		backgroundColor: 'rgba(39, 39, 39, 0.7)',
		borderColor: theme.palette.primary.main,
		borderWidth: '2px',
		borderStyle: 'solid',
	},
	formControl: {
		margin: theme.spacing(5),
		minWidth: 220,
	},
	chartContainer: {
		height: '100%',
		backgroundColor: 'rgba(39, 39, 39, 0.7)',
		borderColor: theme.palette.secondary.main,
		borderWidth: '2px',
		borderStyle: 'solid',
	},
	heading: {
		fontSize: theme.typography.pxToRem(15),
		flexBasis: '40%',
		flexShrink: 0,
		marginRight: '4%',
	},
	secondaryHeading: {
		fontSize: theme.typography.pxToRem(15),
		color: theme.palette.text.secondary,
		flexBasis: '15%',
		flexShrink: 0,
		marginRight: '4%',
	},
	tertiaryHeading: {
		fontSize: theme.typography.pxToRem(15),
		color: theme.palette.text.secondary,
	},
}));

//const data = require('../dataset/ps2_sem1_2020_pref.json');

const ProjectBankPage = () => {
	const classes = useStyles();

	const [data, setData] = useState([]);
	const [dataPoints, setDataPoints] = useState([]);
	const [index, setIndex] = useState({ start: 0, end: 0 });
	const [isNextDisabled, setIsNextDisabled] = useState(true);
	const [isPrevDisabled, setIsPrevDisabled] = useState(true);
	const [choice, setMainChoice] = useState('All');
	const [search, setMainSearch] = useState('');
	const [slider, setMainSlider] = useState([0, 100000]);

	useEffect(() => {
		axios.get('/api/problembank').then((res) => {
			setData(res.data);
		});
	}, []);

	useEffect(() => {
		let newPoints = [];
		data.forEach((d) => {
			if (
				d['Company Name']
					.toLowerCase()
					.includes(search.toLowerCase()) ||
				d['Location'].toLowerCase().includes(search.toLowerCase())
			) {
				if (choice === 'All' || d['Industry Domain'] === choice) {
					if (
						d['Stipend (UG)'] >= slider[0] &&
						d['Stipend (UG)'] <= slider[1]
					) {
						newPoints.push(d);
					}
				}
			}
		});
		if (newPoints.length > 15) {
			setIndex({ start: 0, end: 15 });
			setIsNextDisabled(false);
			setIsPrevDisabled(true);
		} else {
			setIndex({ start: 0, end: newPoints.length });
			setIsPrevDisabled(true);
			setIsNextDisabled(true);
		}
		setDataPoints(newPoints);
	}, [search, choice, slider, data]);

	const handleNext = () => {
		// console.log(allStationInfo);

		if (index.end + 15 < dataPoints.length) {
			setIndex({ start: index.start + 15, end: index.end + 15 });
			setIsPrevDisabled(false);
		} else if (index.start + 15 <= dataPoints.length) {
			setIndex({ start: index.start + 15, end: dataPoints.length });
			setIsPrevDisabled(false);
			setIsNextDisabled(true);
		}
	};

	const handlePrevious = () => {
		if (index.start - 15 > 0) {
			setIndex({ start: index.start - 15, end: index.end - 15 });
			setIsNextDisabled(false);
		} else if (index.end - 15 >= 0) {
			setIndex({ start: 0, end: 15 });
			setIsPrevDisabled(true);
			setIsNextDisabled(false);
		}
	};

	return (
		<div className={classes.root}>
			{window.innerWidth <= '800' ? (
				<Grid
					container
					justify="center"
					alignItems="center"
					alignContent="center"
					style={{ flexGrow: 1, height: '100%', padding: '10px' }}
				>
					<Typography
						component="h5"
						variant="h5"
						color="textSecondary"
						align="center"
					>
						This page is not yet supported on mobile. Please use a
						bigger screen!
					</Typography>
				</Grid>
			) : (
				<Grid
					container
					direction="row"
					spacing={0}
					className={classes.mainComp}
				>
					<Grid
						item
						sm={8}
						xs={12}
						className={classes.chartContainer}
					>
						<Scrollbars
							style={{ height: '100%' }}
							renderThumbVertical={({ style, ...props }) => (
								<div
									{...props}
									style={{
										...style,
										backgroundColor: '#ff6363',
										width: '4px',
										opacity: '0.7',
									}}
								/>
							)}
						>
							{dataPoints
								.slice(index.start, index.end)
								.map((station) => (
									<Accordion
										style={{
											borderBottom: '2px solid #ff6363',
											margin: '15px',
										}}
									>
										<AccordionSummary
											expandIcon={<ExpandMoreIcon />}
											aria-controls="panel1a-content"
											id="panel1a-header"
										>
											<Typography
												className={classes.heading}
											>
												{station['Company Name']}
											</Typography>
											<Typography
												className={
													classes.secondaryHeading
												}
											>
												{station['Location']}
											</Typography>
											{station['Industry Domain'] ===
											'nan' ? (
												<Typography
													className={
														classes.secondaryHeading
													}
												>
													-
												</Typography>
											) : (
												<Typography
													className={
														classes.secondaryHeading
													}
												>
													{station['Industry Domain']}
												</Typography>
											)}
											<Typography
												className={
													classes.tertiaryHeading
												}
											>
												{station['Preferred Branches']}
											</Typography>
										</AccordionSummary>
										<AccordionDetails
											style={{ display: 'block' }}
										>
											<div
												style={{
													position: 'absolute',
													left: '70%',
												}}
											>
												<Link
													underline="none"
													href={
														'/ps2/responses?search=' +
														station['Company Name']
													}
													target="_blank"
												>
													<Button
														variant="outlined"
														color="secondary"
														style={{
															display: 'block',
															marginBottom:
																'10px',
														}}
														fullWidth
													>
														Checkout Responses
													</Button>
												</Link>
												<Link
													underline="none"
													href={
														'/ps2/chronicles?search=' +
														station['Company Name']
													}
													target="_blank"
												>
													<Button
														variant="outlined"
														color="secondary"
														style={{
															display: 'block',
														}}
														fullWidth
													>
														Checkout Chronicles
													</Button>
												</Link>
											</div>

											<div>
												<Typography
													component="span"
													color="secondary"
												>
													Stipend :{' '}
												</Typography>
												<Typography component="span">
													{'₹ ' +
														station['Stipend (UG)']}
												</Typography>
											</div>

											<WriteUp
												bio={station['Projects'].split(
													'\n'
												)}
											/>
										</AccordionDetails>
									</Accordion>
								))}
						</Scrollbars>
					</Grid>
					<Grid item sm={4} xs={12}>
						<Paper elevation={10} className={classes.paper2}>
							<FilterComponentProjectBank
								mainSearch={search}
								setMainChoice={setMainChoice}
								setMainSearch={setMainSearch}
								setMainSlider={setMainSlider}
							/>
						</Paper>
						<div style={{ marginTop: '20px' }}>
							<ButtonSelect
								isPrevDisabled={isPrevDisabled}
								handlePrevious={handlePrevious}
								isNextDisabled={isNextDisabled}
								handleNext={handleNext}
								stations={dataPoints}
								index={index}
							/>
						</div>
						<Grid
							container
							justify="center"
							alignItems="center"
							className={classes.down}
						>
							<CSVLink
								data={data}
								style={{
									textDecoration: 'none',
									width: '100%',
								}}
								filename={'StationDetails.csv'}
							>
								<Button
									variant="outlined"
									color="primary"
									fullWidth
								>
									Download CSV
								</Button>
							</CSVLink>
						</Grid>
					</Grid>
				</Grid>
			)}
		</div>
	);
};

export default ProjectBankPage;
